import * as React from 'react';
import { Tab, Box, Tabs } from '@mui/material';
import { Main, Relative, Cast, About, Top } from './modules';
import {
  ContentScreen,
  AiderScreen,
  FullScreen,
  BodyScreen,
} from '@/layouts/page-layout';
import { useShadowQuery, ShadowQuery } from '@/schema';
import { useParams } from 'react-router-dom';
import { SpecialBox } from '@/components/public/special-box';
import { Helmet } from 'react-helmet';

export const ShadowContext = React.createContext<ShadowQuery | undefined>(
  undefined
);

enum TabStatus {
  INFO = 0,
  RELATIVE = 1,
  ABOUT = 2,
}

export default function Shadow() {
  const { id } = useParams();
  const { data, error, loading } = useShadowQuery({
    variables: {
      id: id!,
    },
  });

  const [tab, setTab] = React.useState(TabStatus.INFO);

  return (
    <Box>
      <Helmet>
        <title>{data?.shadow.title}</title>
      </Helmet>
      {data?.shadow ? (
        <ShadowContext.Provider value={data}>
          <FullScreen>
            <BodyScreen>
              <Top />
            </BodyScreen>
          </FullScreen>
          <BodyScreen>
            <ContentScreen
              sx={(theme) => ({
                display: 'flex',
                flexDirection: 'column',
                backgroundColor: theme.palette.background.paper,
                padding: theme.spacing(1, 6),
                borderRight: `1px solid ${theme.palette.divider}`,
              })}
            >
              <Tabs value={tab} onChange={(_, val) => setTab(val)}>
                <Tab label="信息"></Tab>
                <Tab label="相关推荐"></Tab>
                <Tab label="关于"></Tab>
              </Tabs>
              <Box sx={{ flex: 1 }}>
                <div hidden={tab !== TabStatus.INFO}>
                  <Main />
                </div>
                <div hidden={tab !== TabStatus.RELATIVE}>
                  <Relative />
                </div>
                <div hidden={tab !== TabStatus.ABOUT}>
                  <About html={data?.shadow?.about || ''} />
                </div>
              </Box>
            </ContentScreen>
            <AiderScreen>
              <Cast />
            </AiderScreen>
          </BodyScreen>
        </ShadowContext.Provider>
      ) : (
        <SpecialBox error={!!error} loading={loading} />
      )}
    </Box>
  );
}
