import React from 'react';
import { styled } from '@mui/material/styles';
import { Box, ImageList, ImageListItem } from '@mui/material';
// import { Rating } from "@material-ui/lab";
// import { Favorite } from "@mui/icons-material";
import { MediaOwn } from '@/components/app/media';
import { ShadowContext } from '../..';
import Placeholder from '@/components/base/placeholder';
import { useNavigate } from 'react-router-dom';

const PREFIX = 'Main';

const classes = {
  root: `${PREFIX}-root`,
  box: `${PREFIX}-box`,
};

const StyledBox = styled(Box)(({ theme }) => ({
  [`&.${classes.root}`]: {
    padding: theme.spacing(1, 0),
  },

  [`& .${classes.box}`]: {
    padding: theme.spacing(2, 0),

    '& + &': {
      borderTop: `1px solid ${theme.palette.divider}`,
    },
  },
}));

export const Main: React.FC = () => {
  const navigate = useNavigate();
  const shadowQuery = React.useContext(ShadowContext);

  const handleChangeMedium = (id: string | number) => {
    navigate(`/shadow-play/${shadowQuery?.shadow.id}`, {
      state: { medium_id: id },
    });
  };

  return (
    <StyledBox className={classes.root}>
      {/* <Box className={classes.box}>
        <Typography variant="subtitle1" gutterBottom>
          平均评分
        </Typography>
        <Rating
          readOnly
          value={2}
          precision={0.5}
          icon={<Favorite fontSize="inherit" />}
        />
      </Box> */}
      <Box className={classes.box}>
        {/* <Typography variant="subtitle1" gutterBottom>
          相关资源
        </Typography> */}
        {shadowQuery?.shadow.sources?.length ? (
          <ImageList rowHeight="auto" cols={4}>
            {shadowQuery.shadow.sources.map((source, idx) => {
              return (
                <ImageListItem key={idx} cols={1}>
                  <MediaOwn
                    {...source}
                    onClickRoot={() => handleChangeMedium(source.id)}
                  />
                </ImageListItem>
              );
            })}
          </ImageList>
        ) : (
          <div>
            <Placeholder subtitle="暂无相关资源" />
          </div>
        )}
      </Box>
      {/* <Box className={classes.box}>
        <Typography variant="subtitle1">精彩点评</Typography>
      </Box> */}
    </StyledBox>
  );
};
export default Main;
