import * as React from 'react';
import { styled } from '@mui/material/styles';
import {
  List,
  ListItem,
  ListItemAvatar,
  Avatar,
  ListItemText,
  // ListItemSecondaryAction,
  // IconButton,
  Box,
  ListSubheader,
} from '@mui/material';
// import { MoreVert } from "@mui/icons-material";
import { ShadowContext } from '../..';
import { Placeholder } from '@/components/base/placeholder';

const PREFIX = 'Cast';

const classes = {
  root: `${PREFIX}-root`,
};

const StyledBox = styled(Box)(({ theme }) => ({
  [`&.${classes.root}`]: {
    backgroundColor: theme.palette.background.paper,
  },
}));

export const Cast: React.FC = () => {
  const shadowQuery = React.useContext(ShadowContext);

  return (
    <StyledBox className={classes.root}>
      <List subheader={<ListSubheader>演职表</ListSubheader>}>
        {shadowQuery?.shadow?.credits?.length ? (
          <>
            {shadowQuery.shadow.credits.map((item, i) => {
              return (
                <ListItem key={i}>
                  <ListItemAvatar>
                    <Avatar src={item.avatar}></Avatar>
                  </ListItemAvatar>
                  <ListItemText
                    primary={item.name}
                    secondary={item.description}
                  />
                  {/* <ListItemSecondaryAction>
                    <IconButton>
                      <MoreVert />
                    </IconButton>
                  </ListItemSecondaryAction> */}
                </ListItem>
              );
            })}
          </>
        ) : (
          <Box sx={{ textAlign: 'center' }}>
            <Placeholder subtitle="暂无演职员信息" />
          </Box>
        )}
      </List>
    </StyledBox>
  );
};

export default Cast;
