import * as React from 'react';
import { styled } from '@mui/material/styles';
import { Image } from '@/components/base/image';
import { ShadowContext } from '../..';
import { DEFULAT_SHADOW_COVER } from '@/common/constants/default.constant';
import { Typography, Box, IconButton } from '@mui/material';
import { useNavigate } from 'react-router-dom';
import { PlayCircleOutline } from '@mui/icons-material';
import SwipeableViews from 'react-swipeable-views';
import { autoPlay } from 'react-swipeable-views-utils';

const PREFIX = 'Top';

const classes = {
  root: `${PREFIX}-root`,
  main: `${PREFIX}-main`,
  mainContent: `${PREFIX}-mainContent`,
};

const Root = styled('div')(({ theme }) => ({
  [`&.${classes.root}`]: {
    position: 'relative',
    width: '100%',
  },

  [`& .${classes.main}`]: {
    position: 'absolute',
    top: '50%',
    right: 0,
    width: '50%',
    padding: theme.spacing(0, 8),
    transform: 'translate(0, -50%)',
    color: '#fff',
    display: 'flex',
    flexDirection: 'column',
  },

  [`& .${classes.mainContent}`]: {
    background: 'rgba(0, 0, 0, .1)',
    backdropFilter: 'blur(8px)',
    padding: theme.spacing(4),
    borderRadius: theme.shape.borderRadius,
  },
}));

const AutoPlaySwipeableViews = autoPlay(SwipeableViews);

export const Top: React.FC = () => {
  const shadowQuery = React.useContext(ShadowContext);

  const navigate = useNavigate();
  const [activeStep, setActiveStep] = React.useState(0);

  const goShadowPlay = () => {
    navigate(`/shadow-play/${shadowQuery?.shadow.id}`);
  };

  return (
    <Root className={classes.root}>
      {shadowQuery &&
      shadowQuery.shadow &&
      shadowQuery.shadow.posters?.length ? (
        <AutoPlaySwipeableViews
          onChangeIndex={(step: number) => setActiveStep(step)}
          index={activeStep}
        >
          {shadowQuery.shadow.posters.map((image, idx) => {
            return <Image key={idx} aspectRatio={16 / 9} src={image} />;
          })}
        </AutoPlaySwipeableViews>
      ) : (
        <Image aspectRatio={16 / 9} src={DEFULAT_SHADOW_COVER} />
      )}

      <div className={classes.main}>
        <div className={classes.mainContent}>
          <Typography gutterBottom variant="h4" component="div">
            {shadowQuery?.shadow.title}
          </Typography>
          <Typography gutterBottom variant="subtitle1" component="div">
            {shadowQuery?.shadow.sub_title}
          </Typography>
          {/* <div></div> */}
          <Typography variant="body2" component="div">
            {shadowQuery?.shadow.description}
          </Typography>
          <Box mt={3}>
            <IconButton color="inherit" onClick={goShadowPlay}>
              <PlayCircleOutline fontSize="large" />
            </IconButton>
          </Box>
        </div>
      </div>
    </Root>
  );
};

export default Top;
