import * as React from 'react';
import { styled } from '@mui/material/styles';
import { MediaNormal } from '@/components/app/media';
import { Box, ImageList, ImageListItem } from '@mui/material';
import { useShadowUrgesByShadowQuery } from '@/schema';
import { useParams } from 'react-router-dom';
import { SpecialBox } from '@/components/public/special-box';
import { useNavigate } from 'react-router-dom';

const PREFIX = 'Relative';

const classes = {
  root: `${PREFIX}-root`,
};

const StyledBox = styled(Box)(({ theme }) => ({
  [`&.${classes.root}`]: {
    padding: theme.spacing(2, 0),
  },
}));

export const Relative: React.FC = () => {
  const { id } = useParams();
  const navigate = useNavigate();
  const { data, error, loading } = useShadowUrgesByShadowQuery({
    variables: {
      shadow_id: id!,
    },
  });

  return (
    <StyledBox className={classes.root}>
      {/* <Typography variant="subtitle1" gutterBottom>
        喜欢该视频的也喜欢
      </Typography> */}
      {data?.shadow_urges_by_shadow?.length ? (
        <ImageList rowHeight="auto" cols={4}>
          {data?.shadow_urges_by_shadow.map((shadow, idx) => {
            return (
              <ImageListItem key={idx}>
                <MediaNormal
                  {...shadow}
                  onClickRoot={() => navigate(`/shadow/${shadow.id}`)}
                  onClickAuthor={() =>
                    navigate(`/profile/${shadow.author.username}`)
                  }
                />
              </ImageListItem>
            );
          })}
        </ImageList>
      ) : (
        <div>
          <SpecialBox
            loading={loading}
            error={!!error}
            placeholderTitle="暂无相关推荐"
          />
        </div>
      )}
    </StyledBox>
  );
};

export default Relative;
